.vip-real-time-collaboration-avatars {
	background-color: #fff;
	border-radius: 16px;
	border: 1px solid #ddd;
	padding: 4px 8px;
	position: absolute;
	top: 10px;
	right: 8px;
	pointer-events: auto;

	display: flex;
	align-items: center;

	.vip-real-time-collaboration-avatar {
		margin-left: -10px;

		&:nth-child(1) {
			margin-left: 0;
			z-index: 3;
		}

		&:nth-child(2) {
			z-index: 2;
		}

		&:nth-child(3) {
			z-index: 1;
		}
	}

	.vip-real-time-collaboration-avatar-remaining {
		margin: 0 6px 0 4px;
	}
}

.vip-real-time-collaboration-avatar {
	width: 28px;
	height: 28px;

	img {
		width: 24px;
		height: 24px;
		border-radius: 50%;
		object-fit: cover;
		border: 2px solid #fff;

		/* Fallback for images that haven't loaded yet */
		font-size: 0; // Hide alt text while the image loads.
	}
}

.vip-real-time-collaboration-sidebar-users {
	margin: 8px 0 0 10px;

	.components-flex-item {
		padding: 2px 0;

		&:last-child {
			border-bottom: none;
		}
	}
}

.vip-real-time-collaboration-overlay {
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	pointer-events: none;
	z-index: 20000;

	.vip-real-time-collaboration-overlay-full {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
	}

	.vip-real-time-collaboration-overlay-fixed {
		position: fixed;
		width: 100%;
		height: 100%;
	}
}

.vip-real-time-collaboration-user {
	position: absolute;
}

.vip-real-time-collaboration-user-cursor {
	position: absolute;
	width: 2px;
	animation: vip-real-time-collaboration-cursor-blink 1s infinite;
}

.vip-real-time-collaboration-user-label {
	position: absolute;
	font-size: 11px;
	padding: 2px 6px;
	border-radius: 4px;
	transform: translateY(-100%);
	margin-top: -2px;
	background-color: black;
	color: white;
	white-space: nowrap;
}

@keyframes vip-real-time-collaboration-cursor-blink {
	0%, 100% { opacity: 1; }
	50% { opacity: 0.5; }
}
